import { createApp } from 'vue';
import app from './app.vue';

//  FontAwesome 
function loadFontAwesome() {
  return new Promise<void>(resolve => {
    // 
    if (document.querySelector('link[href*="font-awesome"]') || document.querySelector('link[href*="fontawesome"]')) {
      resolve();
      return;
    }

    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.href = 'https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css';
    link.integrity = 'sha512-iecdLmaskl7CVkqkXNQ/ZH/XLlvWZOJyj7Yy7tcenmpD1ypASozpmT/E0iPtmFIB46ZmdtAc9eNBvH0H/ZpiBw==';
    link.crossOrigin = 'anonymous';
    link.referrerPolicy = 'no-referrer';

    link.onload = () => {
      console.info('FontAwesome ');
      resolve();
    };
    link.onerror = () => {
      console.warn('FontAwesome ');
      resolve(); // 
    };

    document.head.appendChild(link);
  });
}

// 
async function init() {
  try {
    //  FontAwesome
    await loadFontAwesome();

    //  Vue 
    createApp(app).mount('#app');
    console.info('');
  } catch (error) {
    console.error(':', error);
    const appElement = document.getElementById('app');
    if (appElement) {
      appElement.innerHTML = `
        <div style="padding: 20px; color: red; background: #f0f0f0; border-radius: 8px;">
          <h3></h3>
          <p>${error instanceof Error ? error.message : String(error)}</p>
          <p style="font-size: 12px; color: #666; margin-top: 10px;"></p>
        </div>
      `;
    }
  }
}

//  jQuery  errorCatched
if (typeof $ !== 'undefined' && typeof errorCatched !== 'undefined') {
  $(() => {
    errorCatched(init)();
  });
} else if (typeof $ !== 'undefined') {
  //  jQuery errorCatched
  $(() => {
    init();
  });
} else {
  //  DOMContentLoaded
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', init);
  } else {
    init();
  }
}
