            if (earring.value?.equipmentPoints !== undefined) {
                total += earring.value.equipmentPoints;
            }
            return total;
        });
        // ====================  ====================
        /**
         * 0-100
         */
        function calculatePercent(current, max) {
            if (max <= 0)
                return 0;
            return Math.max(0, Math.min(100, (current / max) * 100));
        }
        /**
         * 
         */
        function formatCurrency(value) {
            if (typeof value === 'number') {
                return value.toLocaleString('zh-CN');
            }
            return '0';
        }
        /**
         * CSS
         */
        function getRarityClass(rarity) {
            if (!rarity)
                return 'rarity-common';
            const rarityMap = {
                : 'rarity-common',
                : 'rarity-uncommon',
                : 'rarity-rare',
                : 'rarity-epic',
                : 'rarity-legendary',
                : 'rarity-unique',
                : 'rarity-mythic',
            };
            return rarityMap[rarity] || 'rarity-common';
        }
        /**
         * 
         */
        function getRarityStyle(rarity) {
            if (rarity === '') {
                return {
                    background: 'linear-gradient(135deg, #FF0000, #FFD700)',
                };
            }
            return {};
        }
        /**
         * CSS
         */
        function getQuestStatusClass(status) {
            if (!status)
                return 'quest-status-default';
            const statusMap = {
                : 'quest-status-active',
                : 'quest-status-completed',
                : 'quest-status-failed',
                : 'quest-status-abandoned',
                : 'quest-status-available',
            };
            return statusMap[status] || 'quest-status-default';
        }
        /**
         * 
         */
        function formatCost(cost) {
            if (cost === null || cost === undefined) {
                return '';
            }
            if (typeof cost === 'string') {
                return cost;
            }
            if (typeof cost === 'number') {
                return String(cost);
            }
            if (typeof cost === 'object') {
                const costObj = cost;
                const parts = [];
                // 
                if (costObj. !== undefined || costObj.mp !== undefined) {
                    const mp = costObj. || costObj.mp;
                    parts.push(`: ${mp}`);
                }
                if (costObj. !== undefined || costObj.hp !== undefined) {
                    const hp = costObj. || costObj.hp;
                    parts.push(`: ${hp}`);
                }
                if (costObj. !== undefined || costObj.sanity !== undefined) {
                    const sanity = costObj. || costObj.sanity;
                    parts.push(`: ${sanity}`);
                }
                if (costObj. !== undefined || costObj.stamina !== undefined) {
                    const stamina = costObj. || costObj.stamina;
                    parts.push(`: ${stamina}`);
                }
                // 
                if (parts.length === 0) {
                    Object.keys(costObj).forEach(key => {
                        const value = costObj[key];
                        if (value !== null && value !== undefined) {
                            parts.push(`${key}: ${value}`);
                        }
                    });
                }
                return parts.length > 0 ? parts.join(', ') : JSON.stringify(cost);
            }
            return String(cost);
        }
        /**
         * 
         */
        function extractCurrentValue(value) {
            if (typeof value === 'number')
                return value;
            if (typeof value === 'object' && value !== null) {
                const obj = value;
                if (typeof obj. === 'number')
                    return obj.;
                if (typeof obj. === 'number')
                    return obj.;
            }
            return null;
        }
        /**
         * 
         */
        function extractMaxValue(value) {
            if (typeof value === 'object' && value !== null) {
                const obj = value;
                if (typeof obj. === 'number')
                    return obj.;
                if (typeof obj. === 'number')
                    return obj.;
            }
            return null;
        }
        /**
