                        ? skillObj.description
                        : undefined,
                type: typeof skillObj. === 'string' ? skillObj. : typeof skillObj.type === 'string' ? skillObj.type : undefined,
                rarity: typeof skillObj. === 'string'
                    ? skillObj.
                    : typeof skillObj.rarity === 'string'
                        ? skillObj.rarity
                        : undefined,
                cost: typeof skillObj. !== 'undefined'
                    ? skillObj.
                    : getSkillCostFn
                        ? getSkillCostFn(skillName, isActive)
                        : undefined,
                isActive: isActive,
            };
        }
        // ====================  ====================
